img {
    width: 100%;
    border-radius: 50px 50px 0px 0px;
}


#body {
    background-color: #202026;
}

#root {
    margin: 0px auto 0px auto;
    width: 400px;
    height: 1000px;
    background-color: #18181E;
    border-radius: 50px 50px 0px 0px;
}

#info{
    margin-top: 25px;
    margin-bottom: 25px;
    text-align: center;
    color: ghostwhite;
    line-height: 40px;
}

#info h1, h2, h3, h4, h5, h6 {
    margin: 0px;
}

#info h1 {
    font-family: 'Silkscreen', cursive;
}

#info h3 {
    font-family: 'Inter', sans-serif;
    color: #aaa06b;
}

#buttons{
    display: flex;
    justify-content: space-evenly;
}

#buttons a{
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#buttons a svg{
    margin-left: -20px;
    margin-right: 10px;
}

#mainContent{
    color: darkgray;
    margin-top: 20px;
    margin-left: 10px;
}

#mainContent h3{
    color: ghostwhite;
}

footer{
    background-color: #101025;
    height: 50px;
    align-items: center;
    display: flex;
}

footer svg{
    width: 30px;
    height: 30px;
}

#footer{
    width: 75%;
    display: flex;
    margin: 0px auto 0px auto;
    justify-content: space-evenly;
}

